<template>
	<div class="container">
		<div class="dashboard__header">
			<div class="dashboard__title">
				{{ activeDashboardTitle }}
			</div>
			<div class="dashboard__control">
				<dashboard-control />
			</div>
		</div>
		<section class="dashboard__section">
			<vue-element-loading 
				:active="dashboardLoading"
				:background-color="'rgba(255, 255, 255, .2)'"
				:color="'blue'"
			/>
			<router-view />
			<keep-alive>
				<component
					:is="activeComponent"
					v-if="activeDashboardCategory !== 'user'"
					class="animate__animated animate__fadeIn"
				/>
			</keep-alive>
		</section>
	</div>
</template>

<script>
import ButtonVue from '@/components/BaseButton.vue';
import DashboardControl from '@/components/DashboardControl.vue';
import { componentsImports, componentsTitles } from '@/helpers/сomponentImports';
import VueElementLoading from 'vue-element-loading';
import { mapGetters } from 'vuex';
export default {
	components: {
		ButtonVue,
		DashboardControl,
		'vue-element-loading': VueElementLoading
	},
	props: {

	},
	data() {
		return {
			activeDashboardTitle: '',
			activeComponent: null,
			dashboardLoading: false
		};
	},
	computed: {
		...mapGetters(['getActiveDashboard', 'getActiveDashboardCategory', 'getLastClickedDashboardCategory']),
		activeDashboard() {
			return this.getActiveDashboard;
		},
		activeDashboardCategory() {
			return this.getActiveDashboardCategory;
		},
		lastClickedDashboardCategory() {
			return this.getLastClickedDashboardCategory;
		}
	},
	watch: {
		activeDashboard() {
			if(this.activeDashboard === 'reports') this.$store.commit('setActiveDashboardCategory', 'list');
			this.setActiveComponent();
		},
		activeDashboardCategory() {
			this.setActiveComponent();
		}
	},
	created() {
		this.setActiveComponent();
	},
	methods: {
		setActiveComponent() {
			if(this.activeDashboardCategory === 'user') return;
			this.dashboardLoading = true;
			const component = componentsImports[this.activeDashboard][this.activeDashboardCategory];
			component().then((loadedComponent) => {
				this.activeComponent = loadedComponent.default || loadedComponent;
				this.dashboardLoading = false;
				this.setActiveComponentTitle();
			});
		},
		setActiveComponentTitle() {
			this.activeDashboardTitle = componentsTitles[this.activeDashboard][this.activeDashboardCategory];
		},
	},
};
</script>

<style lang="scss" scoped>
	.dashboard {
		&__header {
			display: flex;
			justify-content: space-between;
			height: 6rem;
		}
		&__title {
			align-self: center;
			font-weight: 600;
			color: #FFFFFF;
		}
		&__control {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__section {
			width: 100%;
			min-height: calc(100vh - 180px);
			border-radius: 9px;
			background-color: #ffffff;
		}
	}
</style>